<template>
  <div v-show="showSta"  class="content">
    <div class="search">
      <div class="searchList">
        <ul>
          <li v-for="(index,value) in searchCrite">
            <span>{{value}}</span>
            <ul>
              <li v-for="it in index">
                <span>{{it}}</span>
                <i class="icon-ok"></i>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <div class="footer">
        <div @click="hide()"><span>取消</span></div>
        <div @click="searchStart()"><span>确定</span></div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name:'searchCrite',
  props:['searchArgs'],
  data(){
    return {
      showSta:false,
      searchCrite:{
        "单据日期":["全部","今天","昨天"],
        "交货日期":['22','21','24'],
        "入库状态":["已入库",'待入库']
      }
    }
  },
  mounted(){
    this.searchCrite=this.searchArgs
    $(".searchList>ul>li:first-child").addClass('active');
    $(".searchList>ul>li:first-child>ul>li:first-child").addClass('active');
    $(".searchList>ul>li>span").click(function(){
        $(this).parent().addClass("active").siblings('.active').removeClass("active");
        $(this).parent().find('ul>li:first-child').addClass("active").siblings('.active').removeClass("active");
    });
    $(".searchList>ul>li>ul>li").click(function(){
      $(this).addClass("active").siblings('.active').removeClass("active");
    });

  },
  methods:{
    show(){
      this.showSta=true
    },
    hide(){
      this.showSta=false
      this.$emit("searchCancel")
    },
    searchStart(){
      this.showSta=false
      var firstPra=$(".searchList>ul>li.active>span").text()
      var secondPra=$(".searchList>ul>li.active>ul>li.active>span").text()
      console.log(firstPra,secondPra)
      this.$emit('searchStart',firstPra,secondPra)
    }
  }
}
</script>

<style lang="less" scoped>
  .content{
    position: absolute;
    left:0;
    top:0;
    padding-top: 55px;
    width: 100%;
    height: 100%;
    z-index: 600;
    .search{
      height: 100%;
      position: relative;
      background: #fff;
      .searchList{
        height: 100%;
        >ul{
          width: 40%;
          background: #eee;
          height: 100%;
          >li{
            font-size: 14px;
            height: 2rem;
            line-height: 2rem;
            text-align: center;
            &.active{
              background: #fff;
              >ul{
                display: block;
                >li.active{
                  >i{display: block;}
                }
              }
            }
            >ul{
              display: none;
              position: absolute;
              top:0;
              left:40%;
              width: 60%;
              height: 100%;
              background: #fff;
              padding-left:.5rem;
              >li{
                height: 2rem;
                line-height: 2rem;
                text-align: left;
                border-bottom: 1px solid #eee;
                &:last-child{
                  border:none;
                }
                >i{
                  color:blue;
                  float:right;
                  line-height:2rem;
                  display: none;
                  margin-right: .5rem;
                }
              }
            }
          }
        }
      }
      .footer{
        position: fixed;
        left:0;
        bottom:0;
        width: 100%;
        display: flex;
        height: 40px;
        background: #fff;
        border-top: 1px solid #eee;
        >div{
          flex:1;
          color:#38ADFF;
          height: 100%;
          line-height: 40px;
          text-align: center;
          border-right: 1px solid #eee;
          &:last-child{
            border-right: none;
          }
        }
      }
    }
  }

</style>



